import React from "react";
import "./DisplayInformation.css"
import {TestNameAndDate} from "./TestNameAndDate"
import {NumberOfCorrectAnswers} from "./NumberOfCorrectAnswers"
export class DisplayInformation extends React.Component {
    render() {
      return (
        <div className="test-info">
          <TestNameAndDate
            testSelected={this.props.testSelected}
          ></TestNameAndDate>
          <NumberOfCorrectAnswers
            passStateTwo={this.props.passState}
            passTestLength={this.props.testLength}
          ></NumberOfCorrectAnswers>
        </div>
      );
    }
  }