import React from "react";
import "./Question.css"
import {CorrectQuestion,WrongQuestion,UnAnsweredQuestion} from "./Question"
export class QuestionContainer extends React.Component {
    render() {
      const questionContainer = this.props.passStateTwo.answeredQuestion.map(
        (question, index) => {
          if (question === 1) {
            return (
              <li
                onClick={this.props.passQuestionButton}
                className="drop dropdown-item"
              >
                <CorrectQuestion order={index}></CorrectQuestion>
              </li>
            );
          } else if (question === 0) {
            return (
              <li
                onClick={this.props.passQuestionButton}
                className="drop dropdown-item"
              >
                <WrongQuestion order={index}></WrongQuestion>
              </li>
            );
          }
          return (
            <li
              onClick={this.props.passQuestionButton}
              className="drop dropdown-item"
            >
              <UnAnsweredQuestion order={index}></UnAnsweredQuestion>
            </li>
          );
        }
      );
      return (
        <div className="questions-drop-down">
          <i className="material-icons">pets</i>
          <div className="parent-dropdown">
            <ul>
              <li className="drop dropdown-container">
                <div style={{ display: "flex" }}>
                  <p className="question-dropdown">
                    Câu hỏi <span>{this.props.passStateTwo.question + 1}</span>/
                    <span>{this.props.passTestLength}</span>
                  </p>
                  <i className="dropdown-icon material-icons">arrow_drop_down</i>
                </div>
                <div className="underline"></div>
                <ul className="dropdown" aria-label="submenu">
                  {questionContainer}
                </ul>
              </li>
            </ul>
          </div>
        </div>
      );
    }
  }