import React from "react";
import "./Answer.css"
import {Answer,HoverableAnswer,CorrectAnswer,WrongAnswer} from "./Answer"
export  class AnswerContainer extends React.Component {
    render() {
      const answerContainer = this.props.answers.map((answer, index) => {
        if (this.props.passStateTwo.chosen && this.props.passStateTwo.correct) {
          if (answer[2] === 1) {
            return (
              <li>
                <CorrectAnswer answer={answer}></CorrectAnswer>
              </li>
            );
          } else {
            return (
              <li>
                <Answer
                  passAnswerButtonTwo={this.props.passAnswerButton}
                  answer={answer}
                ></Answer>
              </li>
            );
          }
        }
        if (this.props.passStateTwo.chosen && !this.props.passStateTwo.correct) {
          if (answer[2] === 1) {
            return (
              <li>
                <CorrectAnswer answer={answer}></CorrectAnswer>
              </li>
            );
          } else if (answer[0] === this.props.passStateTwo.character) {
            return (
              <li>
                <WrongAnswer answer={answer}></WrongAnswer>
              </li>
            );
          } else {
            return (
              <li>
                <Answer
                  passAnswerButtonTwo={this.props.passAnswerButton}
                  answer={answer}
                ></Answer>
              </li>
            );
          }
        }
        return (
          <li>
            <HoverableAnswer
              passAnswerButtonTwo={this.props.passAnswerButton}
              answer={answer}
            ></HoverableAnswer>
          </li>
        );
      });
  
      return <ul className="answers">{answerContainer}</ul>;
    }
  }