import React from "react";
import {PaginationButton} from "../../button/Button"
import {TestItem} from "./Test-item/TestItem"
import demoTests from "../../../demoTests"
export class TestListContainer extends React.Component {
  createTestArray=(page = 1, resPerPage = 3)=>{
    ///testArray chua thong tin nhu ngay tao, ten
    let testArray=[]
    let testArrayStorage=JSON.parse(localStorage.getItem("testArray"))
    const start = (page - 1) * resPerPage;
    const end = page * resPerPage;
    ////------------------real test----------------/////
    // if(!testArrayStorage){

    // } else {
    
    //   // testArrayStorage.slice(start, end).forEach(testNumber=>{
    //   //   // const test=JSON.parse(localStorage.getItem(`testNo${testNumber}`))
    //   //   // testArray.push(test)        
    //   // })
    // }


    ////------------------demo test----------------/////
    
    demoTests.slice(start,end).forEach(test=>{
      testArray.push(demoTests[test.testNumber-1])  
    })
    return testArray
  }
  renderButton=(page, numResults, resPerPage)=>{
    const pages = Math.ceil(numResults / resPerPage);
    if (page === 1 && pages > 1) {
        // Only button to go to next page
        return (
          <div className="page-button-container">
            <PaginationButton
              paginationButton={this.props.paginationButton}
              page={page}
              type="next"
            ></PaginationButton>
          </div>
        )
    } else if (page < pages) {
        // Both buttons
        return (
          <div className="page-button-container">
            <PaginationButton
              paginationButton={this.props.paginationButton}
              page={page}
              type="prev"
            ></PaginationButton>
            <PaginationButton
              paginationButton={this.props.paginationButton}
              page={page}
              type="next"
            ></PaginationButton>
          </div>
        )
    } else if (page === pages && pages > 1) {
        // Only button to go to prev page
        return (
          <div className="page-button-container">
            <PaginationButton
              paginationButton={this.props.paginationButton}
              page={page}
              type="prev"
            ></PaginationButton>
          </div>
        )
    }
  }
  render() {
    const button=this.renderButton(this.props.page,this.props.numResults,3)
    const testArray= this.createTestArray(this.props.page,4)
    const tests= testArray.map(test=>{

      if(test.testNumber===this.props.testSelected){
        return <TestItem
                testSelected={true}
                test={test}
                key={test.testNumber}
              ></TestItem>
      }
      else return <TestItem
                    testSelected={false}
                    test={test}
                    key={test.testNumber}
                    testClicked={this.props.testClicked}
                  ></TestItem>
    })
    const yourTestListStyle={
        display:"flex",
        marginBottom: "1rem",
        marginLeft: "1rem",
        marginTop: "2rem"
    }
    return (
      <div>
        <div style={yourTestListStyle}>
          <i className="material-icons">library_add</i>
          <p style={{ marginLeft: "1rem" }}>Đề thi của bạn</p>
        </div>
        {tests}
        <div className="page-button-container-container">{button}</div>
      </div>
     
    );
  }
}
