import React from "react";
import "./Answer.css"
export class Answer extends React.Component {
    render() {
      return (
        <div className="answer">
          <div className="answer__character">
            <p>{this.props.answer[0]}</p>
          </div>
          <p className="answer__choice">{this.props.answer[1]}</p>
          <p className="answer__result">{this.props.answer[2]}</p>
        </div>
      );
    }
  }
  export class HoverableAnswer extends React.Component {
    render() {
      return (
        <div
          onClick={this.props.passAnswerButtonTwo}
          className="answer answer-hoverable"
        >
          <p className="answer__character">{this.props.answer[0]}</p>
          <p className="answer__choice">{this.props.answer[1]}</p>
          <p className="answer__result">{this.props.answer[2]}</p>
        </div>
      );
    }
  }
  export class CorrectAnswer extends React.Component {
    render() {
      return (
        <div className="correct-answer answer">
          <p className="answer__character display-none">{this.props.answer[0]}</p>
          <div className="answer__character">
            <img
              className="check-error-img__character"
              src={process.env.PUBLIC_URL + `/icon-quiz/check.png`}
              alt="check-img"
            />
          </div>
          <p className="answer__choice">{this.props.answer[1]}</p>
          <p className="answer__result">{this.props.answer[2]}</p>
          <p className="answer__result-word" style={{ color: "#267131" }}>
            Câu đúng
          </p>
        </div>
      );
    }
  }
  export class WrongAnswer extends React.Component {
    render() {
      return (
        <div className="wrong-answer answer">
          <p className="answer__character display-none">{this.props.answer[0]}</p>
          <div className="answer__character">
            <img
              className="check-error-img__character"
              src={process.env.PUBLIC_URL + `/icon-quiz/error.png`}
              alt="error-img"
            />
          </div>
          <p className="answer__choice">{this.props.answer[1]}</p>
          <p className="answer__result">{this.props.answer[2]}</p>
          <p className="answer__result-word" style={{ color: "red" }}>
            Câu bạn chọn
          </p>
        </div>
      );
    }
  }