import React from "react";
import "./TestItem.css"
export class TestItem extends React.Component{
    render() {
      return (
        <div className={`display-test-list ${this.props.testSelected? "test-selected":""}`} onClick={this.props.testClicked}>
          <i className="material-icons">library_books</i>
          <div className="test-name-date">
            <p style={{ marginLeft: "1rem", fontSize:".8rem"}} className="test-name">{this.props.test.testName}</p>
            <p style={{ marginLeft: "1rem" }} className="test-date">{this.props.test.date}</p>
            <p className="test-number" style={{display:"none"}}>{this.props.test.testNumber}</p>
          </div>
          <i style={{marginLeft:"3rem"}} className="remove-icon material-icons" >remove_circle_outline</i>
        </div>
      );
    }
  }
