import React from "react";
import {RetryButton} from "../../../button/Button"
import "../Dialog/Dialog.css"
export class Result extends React.Component {
    render() {
      return (
        <div className="display-result display-quizzes">
          <p className="display-result__title">
            Bạn đã hoàn thành bài trắc nghiệm
          </p>
          <img
            className="ending-img"
            src={window.location.origin + "/ending.jpg"}
            alt="ending-img"
          />
          <p className="display-result__yourScore">Điểm của bạn</p>
          <p
            style={{ marginBottom: "1rem" }}
            className="quiz-correct-answers__display"
          >
            <span>
              {this.props.passState.answeredQuestion.filter(x => x === 1).length}
            </span>
            /<span>{this.props.testLength}</span>
          </p>
          <RetryButton passRetryButton={this.props.retryButton}></RetryButton>
        </div>
      );
    }
  }