import React from "react";
import "./Dialog.css"
import {StartButton,EndButton,ContinueButton} from "../../../button/Button"
import demoTests from "../../../../demoTests"
export class DisplayStartDialog extends React.Component {
  returnName(){
    return demoTests[this.props.testSelected-1].testName
  }  
  render() {
    let name=this.returnName()
      return (
        <div className="display-result display-quizzes">
          <img
            className="start-img"
            src={process.env.PUBLIC_URL + "/start.jpg"}
            alt="start-img"
          />
          <p className="display-result__yourScore" style={{fontSize:"3rem"}}>{name}</p>
          <p className="display-result__yourScore" style={{marginBottom:"2rem"}}>Bài trắc nghiệm này có <span>{this.props.testLength}</span> câu hỏi</p>
          <StartButton
            startButton={this.props.startButton}
          ></StartButton>
        </div>
      );
    }
  }
  export class DisplayEndDialog extends React.Component {
    render() {
      return (
        <div className="display-dialog-container">
          <div className="display-dialog">
            <p className="display-dialog__title">
              Bạn vẫn còn câu chưa điền. Kết thúc?
            </p>
            <div class="end-continue-button-container">
              <EndButton passEndButtonTwo={this.props.passEndButton}></EndButton>
              <ContinueButton
                passContinueButtonTwo={this.props.passContinueButton}
              ></ContinueButton>
            </div>
          </div>
        </div>
      );
    }
  }
  