import React from "react";
import "./Button.css"
export class NextButton extends React.Component {
    render() {
      return (
        <div onClick={this.props.passNextButton} className="next-button">
          {this.props.passStateTwo.question + 1 !== this.props.passTestLength
            ? "Câu tiếp theo"
            : "Kết thúc"}
        </div>
      );
    }
  }
  export class RetryButton extends React.Component {
    render() {
      return (
        <div onClick={this.props.passRetryButton} className="next-button">
          Làm lại
        </div>
      );
    }
  }
  export class StartButton extends React.Component {
    render() {
      return (
        <div onClick={this.props.startButton} className="next-button">
          Bắt đầu
        </div>
      );
    }
  }
  export class EndButton extends React.Component {
    render() {
      return (
        <div
          onClick={this.props.passEndButtonTwo}
          className="end-continue-button"
        >
          KẾT THÚC
        </div>
      );
    }
  }
  export class ContinueButton extends React.Component {
    render() {
      return (
        <div
          onClick={this.props.passContinueButtonTwo}
          className="end-continue-button"
        >
          TIẾP TỤC
        </div>
      );
    }
  }
  export class PaginationButton extends React.Component {
    render(){
      return (
        <button onClick={this.props.paginationButton} className={`page-button results__btn--${this.props.type}`} data-goto={this.props.type === 'prev' ? this.props.page - 1 : this.props.page + 1}>
          <span>{`Page ${this.props.type === 'prev' ? this.props.page - 1 : this.props.page + 1}`}</span>
          <i className="material-icons">{`chevron_${this.props.type === 'prev' ? 'left' : 'right'}`}</i>
        </button>
      )
    }
  }
  export class AddTestButton extends React.Component{
    render() {
      return (
        <div
          className="add-test-button"
          onClick={this.props.addTestButton}
        >
          Thêm
        </div>
      );
    }
  }
  