import React from "react";
import "./TestNameAndDate.css"
export class TestNameAndDate extends React.Component {
    render() {
      let testDisplay=JSON.parse(localStorage.getItem(`testNo${this.props.testSelected}`))
      if(testDisplay){
        return (
          <div className="test-name-and-date">
            <img
              className="test-img"
              src={process.env.PUBLIC_URL + `/icon-quiz/book.png`}
              alt="book-img"
            />
            <div>
              <p className="test-name">{testDisplay.testName}</p>
              <p className="test-date">{testDisplay.date}</p>
            </div>
          </div>
        );
      } else return (
        <div className="test-name-and-date">
            <img
              className="test-img"
              src={process.env.PUBLIC_URL + `/icon-quiz/book.png`}
              alt="book-img"
            />
            <div>
              <p className="test-name"></p>
              <p className="test-date"></p>
            </div>
          </div>
      )
      
    }
  }