import React from "react";
import "./Question.css"

export class CorrectQuestion extends React.Component {
    render() {
      const number = parseInt(this.props.order) + 1;
      return (
        <div className="question-container">
          <p
            style={{ color: "#267131" }} className="question-li"
          >
            {number} - Đúng
          </p>
          <img
            className="check-error-img"
            src={window.location.origin + "/icon-quiz/check.png"}
            alt="check-img"
          />
        </div>
      );
    }
  }
  export class WrongQuestion extends React.Component {
    render() {
      const number = parseInt(this.props.order) + 1;
      return (
        <div className="question-container">
          <p className="question-li" style={{ color: "red" }}>
            {number} - Sai
          </p>
          <img
            className="check-error-img"
            src={window.location.origin + "/icon-quiz/error.png"}
            alt="error-img"
          />
        </div>
      );
    }
  }
  export class UnAnsweredQuestion extends React.Component {
    render() {
      const number = parseInt(this.props.order) + 1;
      return (
        <p className="question-li">{number} - Chưa trả lời</p>
      );
    }
  }