import React from "react";
import "./Form.css"
import {AddTestButton} from "../button/Button"
export class Form extends React.Component{
    render() {
      return (
        <div className="form-container">
          <p className="form__create-text">Tạo đề thi</p>
          <form className="form">
            <input 
              type="text" 
              value={this.props.testLink} 
              name="testLink" 
              placeholder="Nhập link đề thi" 
              onChange={this.props.handleChange}
              className="input link-input"
            />
            <input 
              value={this.props.testName}
              name="testName"
              onChange={this.props.handleChange}
              placeholder="Nhập tên đề thi" 
              className="input"
            />
            <AddTestButton addTestButton={this.props.addTestButton}></AddTestButton>
          </form>
        </div>
      )
    }
  }