import React from "react";
import "./NumberOfCorrectAnswers.css"
export class NumberOfCorrectAnswers extends React.Component {
    render() {
      return (
        <div className="quiz-correct-answers">
          <img
            className="check-img"
            src={process.env.PUBLIC_URL + `/icon-quiz/check.png`}
            alt="check-img"
          />
          <p className="quiz-correct-answers__display">
            <span>
              {
                this.props.passStateTwo.answeredQuestion.filter(x => x === 1)
                  .length
              }
            </span>
            /<span>{this.props.passTestLength}</span>
          </p>
        </div>
      );
    }
  }